/* Global */
:root {
  --main-color: #691551;
  --second-color: #af3d8f;
  --third-color: #a5488a;
  --widget-bg: #e8eafcad;
}
[data-theme="purple"] {
  --primary: var(--main-color);
  --dt-primary-color: var(--main-color);
  --blue-avatar-bg: var(--main-color);
  --blue-avatar-color: var(--main-color);
  --blue: var(--main-color);
}

[data-theme="purple"] .page-container,
[data-theme="purple"] .layout-main-section {
  background-color: var(--widget-bg);
}
[data-theme="purple"] ul li a:hover {
  background-color: var(--second-color);
  color: #fff;
}
/* Main */
[data-theme="purple"] body {
  font-size: 14px;
  background-color: #f3f6f9;
}
[data-theme="purple"]a {
  color: #666;
  font-weight: 500;
}
[data-theme="purple"] .small,
small {
  font-size: 95%;
}
[data-theme="purple"] .h6.uppercase,
h6.uppercase {
  font-weight: 600;
  font-size: 11.5px;
}

/* Navigation Bar */
[data-theme="purple"] .standard-sidebar-item.selected {
  background: linear-gradient(to right, var(--main-color), var(--second-color));
}
[data-theme="purple"] .standard-sidebar-item.selected a {
  color: #fff;
}
[data-theme="purple"] .standard-sidebar-item.selected a svg {
  fill: #fff;
}
[data-theme="purple"] .standard-sidebar-item:hover {
  background-color: var(--second-color);
}
[data-theme="purple"] .standard-sidebar-item:hover a,
[data-theme="purple"] .standard-sidebar-item:hover svg {
  color: #fff;
  fill: #fff;
}

[data-theme="purple"] .navbar {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--second-color)
  ) !important;
  border: none !important;
}
[data-theme="purple"] .navbar-nav > li > a {
  color: #fff !important;
}
[data-theme="purple"] .navbar-nav > li > a:hover {
  color: #ddd !important;
}
[data-theme="purple"] .navbar-brand:hover {
  color: #ddd !important;
}

/* Widgets */
[data-theme="purple"] .widget {
  background-color: var(--widget-bg);
  border: none;
  box-shadow: 0px 3px 12px 0px var(--widget-bg), 0 3px 3px -2px #461a7714;
}
[data-theme="purple"] .widget.shortcut-widget-box .widget-head .widget-title i {
  color: var(--third-color);
}
[data-theme="purple"]
  .widget.shortcut-widget-box
  .widget-head
  .widget-title:hover
  span {
  color: var(--main-color);
}
[data-theme="purple"] .widget-body .link-item:hover {
  color: #fff;
  background: var(--third-color);
}
/* Sidebar */
[data-theme="purple"] .desk-sidebar .desk-sidebar-item {
  font-size: 13px;
  font-weight: 600;
  color: var(--widget-bg);
}
[data-theme="purple"] .layout-side-section {
  font-size: 14px;
}
[data-theme="purple"] .uppercase {
  color: var(--third-color);
}

/* Dropdowns */
[data-theme="purple"] .dropdown-menu > li > a {
  font-size: 13px;
  color: #666;
  font-weight: 500;
}
[data-theme="purple"] .awesomplete > ul > li[aria-selected="true"] {
  background-color: transparent;
}
[data-theme="purple"] .awesomplete > ul > li:hover {
  background: linear-gradient(to right, var(--main-color), var(--second-color));
}
[data-theme="purple"] .awesomplete > ul > li:hover a {
  color: #fff;
}
/* Page Heading */
[data-theme="purple"] .page-head {
  border-bottom: none !important;
  box-shadow: 0 1px 10px 1px rgba(115, 108, 203, 0.1);
}
[data-theme="purple"] .page-title .title-text {
  color: var(--third-color);
  font-size: 22px;
}
[data-theme="purple"] .widget .widget-head .widget-title,
[data-theme="purple"] .section-head,
[data-theme="purple"] .comment-input-header {
  font-size: 14px;
  color: var(--third-color);
}

/*Main Section */
[data-theme="purple"] .list-row-head {
  background-color: #f3f6f9;
  border-bottom: 1px solid #e4e4e4 !important;
}

/* Buttons */
[data-theme="purple"] .btn-primary {
  background-color: var(--main-color);
  border-color: var(--main-color);
}
[data-theme="purple"] .control-label,
.grid-heading-row {
  font-size: 13px;
}
[data-theme="purple"] .btn-default:hover,
[data-theme="purple"] .btn-default:focus,
.btn-default.focus,
[data-theme="purple"] .btn-default:active,
.btn-default.active,
[data-theme="purple"] .open > .dropdown-toggle.btn-default {
  background-color: var(--third-color) !important;
  color: #fff !important;
}
[data-theme="purple"] .btn-default:hover svg {
  stroke: #fff !important;
  fill: #fff !important;
  color: #fff !important;
}
[data-theme="purple"] .btn-primary:hover,
[data-theme="purple"] .btn-primary:focus,
.btn-primary.focus,
[data-theme="purple"] .btn-primary:active,
.btn-primary.active,
[data-theme="purple"] .open > .dropdown-toggle.btn-primary {
  background-color: var(--main-color) !important;
  border-color: var(--main-color) !important;
}
[data-theme="purple"] .btn-secondary,
[data-theme="purple"] .btn-secondary:focus,
.btn-secondary.focus,
[data-theme="purple"] .btn-secondary:active,
.btn-secondary.active,
[data-theme="purple"] .open > .dropdown-toggle.btn-secondary {
  background-color: var(--third-color);
  color: #fff;
}

[data-theme="purple"] .btn-secondary:hover {
  background-color: var(--main-color);
  color: #fff;
}
[data-theme="purple"] .indicator-right.blue:after,
.indicator.blue:before {
  background: var(--third-color);
}

/*Other Components*/
[data-theme="purple"] input[type="checkbox"]:checked:before {
  color: var(--third-color);
}
[data-theme="purple"] input[type="checkbox"]:checked {
  background: var(--main-color);
}
[data-theme="purple"] .frappe-control .ql-editor:not(.read-mode) {
  background: var(--widget-bg);
}
[data-theme="purple"] .input-with-feedback,
[data-theme="purple"] .like-disabled-input {
  background-color: #e8eafcad;
}
[data-theme="purple"] .document-link-badge span {
  background-color: var(--main-color) !important;
  color: #fff;
}

[data-theme="purple"] .document-link-badge {
  background-color: var(--widget-bg) !important;
}
[data-theme="purple"] .document-link button {
  background: var(--third-color) !important;
  color: #fff !important;
}

[data-theme="purple"] .document-link button svg {
  fill: white;
  stroke: white;
}
[data-theme="purple"] .indicator-pill.gray,
[data-theme="purple"] .indicator-pill-right.gray,
[data-theme="purple"] .indicator-pill-round.gray {
  background: var(--third-color);
  color: #fff;
}
[data-theme="purple"] .indicator-pill.green,
[data-theme="purple"] .indicator-pill-right.green,
[data-theme="purple"] .indicator-pill-round.green {
  color: var(--main-color);
}

[data-theme="purple"] .notifications-icon svg {
  stroke: #fff;
  fill: #fff;
}

[data-theme="purple"] #page-Workspaces .page-head {
  background: var(--widget-bg);
}
